
import { defineComponent } from "vue";
import Pagination from '@/shared/components/Pagination/Pagination.vue'
import type { COM20600111_RES, COM20600111_REQ, PortalServiceList } from "@/shared/type/COM20600111.type";
import { PAGE_SIZE } from "@/shared/constants/common.const";
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";
import { ConvertCode } from '@/shared/services/ConvertCodeService';
const convertCode = new ConvertCode();
export default defineComponent({
    name: 'COM206001SC00',
    components: {
        Pagination,
        BreadCrumb,
        SelectOption
    },

    data() {
        const header = [
            { columnName: this.$t('com206001sc00.column.no') },
            { columnName: this.$t('com206001sc00.column.view') },
            { columnName: this.$t('com206001sc00.column.service_id') },
            { columnName: this.$t('com206001sc00.column.service_name') },
            { columnName: this.$t('com206001sc00.column.description') },
            { columnName: this.$t('com206001sc00.column.status') },
            { columnName: 'Check Service YN' },
        ];
        const portalServiceList = [] as PortalServiceList[];
        return {
            convertCode,
            header,
            portalServiceList,
            transactionControlStatusCode: '',
            serviceIDName: '',
            serviceID: '',
            checkServiceYN: '',
            totalItems: 0,
            currentPage: 1,
            pageSize: PAGE_SIZE,
            startingIndex: 1,
            isReset: false
        };
    },

    mounted() {
        this.requestPortalServiceList();
    },

    computed: {
        ScreenStatusCode() {
            return this.$store.state.CommonCodeModule.commonCode.ScreenStatusCode
        },

        transactionControlStatusCodeValue() {
            return (statusCode: string, statusValue: string) => `${statusCode}` + ' : ' + `${statusValue}`;
        }
    },

    methods: {
        async requestPortalServiceList() {
            const reqBody = {
                serviceIDName: this.serviceIDName,
                serviceID: this.serviceID,
                pageSize: this.pageSize,
                pageNumber: this.currentPage,
                checkServiceYN: this.checkServiceYN,
                transactionControlStatusCode: this.transactionControlStatusCode
            } as COM20600111_REQ;
            const res = await this.$request.getRequest('COM20600111', reqBody, false) as COM20600111_RES;

            this.portalServiceList = res?.body.list.map((item: PortalServiceList) => {

                return {
                    serviceID: item.serviceID,
                    serviceIDName: item.serviceIDName,
                    serviceIDDescription: item.serviceIDDescription,
                    checkServiceYN: item.checkServiceYN,
                    transactionControlStatusCode: this.transactionControlStatusCodeValue(item.transactionControlStatusCode, this.$codeConverter.convertValue("ScreenStatusCode", item.transactionControlStatusCode)) // first paremeter accept menuStatusCode and second paremeter accept menuStatusCode too but already covert to value 
                }
            });
            this.totalItems = res.body.totalCount;
        },

        handleRowClick(item) {
            this.$router.push({
                path: `COM206001SC01`,
                query: {
                    type: "edit",
                    serviceID: item.serviceID
                }
            })
        },

        handleClickAction(actionType: string) {
            switch (actionType) {
                case 'register':
                    this.$router.push({
                        path: `COM206001SC01`,
                        query: { type: "register" }
                    })
                    break;
                case 'search':
                    if (this.currentPage > 1) {
                        this.currentPage = 1
                        this.isReset = true
                        this.requestPortalServiceList();
                    } else {
                        this.requestPortalServiceList();
                    }
                    break;
                default:
                    this.currentPage = 1
                    this.serviceID = '';
                    this.serviceIDName = '';
                    this.checkServiceYN = ''
                    this.transactionControlStatusCode = '';
                    this.isReset = true;
                    this.requestPortalServiceList();
                    break;
            }
        },

        handlePagination(pageNumber: number) {
            this.isReset = false;
            this.currentPage = pageNumber;
            this.requestPortalServiceList();
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex;
        }
    },
});

